"use client";

export function SignOutButton() {
  const handleSignOut = () => {
    window.location.href = "/api/auth/signout";
  };

  return (
    <button
      onClick={handleSignOut}
      className="px-4 py-2 bg-red-600 text-white rounded hover:bg-red-500"
    >
      Sign Out
    </button>
  );
}